body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.instruction {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;

}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.game {
  margin:5%;
  background: #fff;
}

circle {
  fill: red;
}

rect {
  fill : black;

}

.node {
  fill: yellow;
}


.screen {
  background: green;
  display: flex;
}

.gameover {
  background: red;
  color:#fff;
  align-items: center;
  justify-content: center;

}

.text-center {
  color:#fff;
  font-size: 50px;
  text-anchor: middle;
}

.control-input {
  display: flex;
  color: #000;
  font-size: 14px;
  margin-top: 10px;
}

.control-input-entry {
  margin-right: 5px;
}
